import interfaces from '../../utils/interface.js';
import jifenHead from './base/head.vue'
import jifenList from './base/jifenList.vue'
export default {
	data() {
		return {
			categoryList: [], //分类列表
		}
	},
	components: {
		jifenHead,
		jifenList
	},
	onLoad() {
		this.initData();
		this.$refs.touch.get_time()
	},
	methods: {
		async initData() {
			this.request({
				url: interfaces.get_bigclass, //获取分类名称
				success: ((res) => {
					if (res.error_code == 0) {
						this.categoryList = res.data;
					}
				})
			});
		}
	},
	onPullDownRefresh() {
		setTimeout(() => {
			uni.stopPullDownRefresh();
			this.initData();
		}, 1000);
	},
}
